<template>
  <div class="box">
    <div
      class="flex row ra"
    >
      <button
        v-if="certificate.id && !certificate.available"
        class="button is-link mr-3"
        type="button"
        :class="{'is-loading' : loading_publish}"
        :disabled="changes != 0"
        :title="changes != 0 ? 'Deve guardar o bilhete antes de o emitir' : 'Um bilhete emitido fica automáticamente disponível para todos os participantes com inscrição validada e participantes que futuramente tenham a sua inscrição validada.'"
        @click="activate()"
      >
        <span class="icon"><i class="fas fa-upload" /></span>
        <span>Emitir</span>
      </button>
      <button
        v-if="certificate.id && certificate.available"
        class="button is-link mr-3"
        type="button"
        :class="{'is-loading' : loading_publish}"
        :disabled="changes != 0"
        :title="changes != 0 ? 'Deve guardar o bilhete antes de o emitir' : 'Um bilhete cuja emissão é cancelada passa a estar indisponível para impressão, mas se for emitido novamente os códigos de validação mantém-se e bilhetes já emitidos podem continuar a ser usados.'"
        @click="activate();"
      >
        <span class="icon"><i class="fas fa-download" /></span>
        <span>Cancelar emissão</span>
      </button>
      <button
        class="button is-primary"
        type="button"
        onclick="window.print()"
      >
        <span class="icon"><i class="fas fa-print" /></span>
        <span>Pré-visualizar</span>
      </button>
    </div>
    <div
      id="printarea"
    >
      <div>
        <div
          class="flex my-column"
        >
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca half-width"
              style="padding: 5mm;"
            >
              <img
                src="@/assets/ualg.svg"
                style="width: 80mm"
              >
              <div class="spacer" />
              <span><b>Deve ter este bilhete consigo no momento de entrada no recinto do evento.</b></span>
              <div class="half-spacer" />
              <span>O bilhete pode ser impresso ou usado em formato digital. Se perder o bilhete deve proceder ao pedido da emissão de um novo bilhete na plataforma de eventos.</span>
              <div class="half-spacer" />
              <span>O código QR neste bilhete serve como comprovativo de inscrição. Não deve partilhá-lo com ninguém, excepto com a organização ao fazer o registo da sua presença.</span>
            </div>
            <hr class="is-hidden-print">
            <div class="half-width">
              <div
                id="qrcode"
                class="flex my-column ca vca"
              >
                <h2 class="is-size-2">
                  Código de registo de presença
                </h2>
                <vue-qrcode
                  :value="'Apenas um exemplo, não pode ser usado para registo de presença'"
                  :options="{ width: 300 }"
                />
              </div>
            </div>
          </div>
          <hr class="is-hidden-print">
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca inverted half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-3">
                Identificação
              </h2>
              <div class="spacer" />
              <h3 class="is-size-2">
                Nome do participante
              </h3>                
            </div>
            <hr class="is-hidden-print">
            <div
              class="flex my-column ca vca inverted  half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-2">
                Dados do Evento
              </h2>
              <h3 class="is-size-3">
                {{ certificate.event_name }}
              </h3>
              <div class="spacer" />
              <span v-if="certificate.start"><b>Início esperado:</b> {{ certificate.start }}</span>
              <span v-if="certificate.end"><b>Fim esperado:</b> {{ certificate.end }}</span>
              <span v-if="certificate.location"><b>Localização:</b> {{ certificate.location }}</span>
              <br>
              <br>
              <span><b>Acesso Wi-Fi</b></span>
              <br>
              <span><b>SSID: </b> ualg-conferencias</span>
              <span><b>Password: </b> Não sei :(</span>
              <br><br>
              <span v-if="certificate.emergencyContact1 || certificate.emergencyContact2"><b>Contactos de emergência</b></span>
              <br>
              <span v-if="certificate.emergencyContact1">{{ certificate.emergencyContact1 }}</span>
              <span v-if="certificate.emergencyContact2">{{ certificate.emergencyContact2 }}</span>
            </div>
            <hr class="is-hidden-print">
          </div>
        </div>
      </div>
    </div>
    <form
      id="editarea"
      ref="form"
      @submit.prevent="submit()"
    >
      <div>
        <div
          class="flex my-column"
        >
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca half-width"
              style="padding: 5mm;"
            >
              <img
                src="@/assets/ualg.svg"
                style="width: 80mm"
              >
              <div class="spacer" />
              <span><b>Deve ter este bilhete consigo no momento de entrada no recinto do evento.</b></span>
              <div class="half-spacer" />
              <span>O bilhete pode ser impresso ou usado em formato digital. Se perder o bilhete deve proceder ao pedido da emissão de um novo bilhete na plataforma de eventos.</span>
              <div class="half-spacer" />
              <span>O código QR neste bilhete serve como comprovativo de inscrição. Não deve partilhá-lo com ninguém, excepto com a organização ao fazer o registo da sua presença.</span>
            </div>
            <hr class="is-hidden-print">
            <div class="half-width">
              <div
                id="qrcode"
                class="flex my-column ca vca"
              >
                <h2 class="is-size-2">
                  Código de registo de presença
                </h2>
                <vue-qrcode
                  :value="'Apenas um exemplo, não pode ser usado para registo de presença'"
                  :options="{ width: 300 }"
                />
              </div>
            </div>
          </div>
          <hr class="is-hidden-print">
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca inverted half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-3">
                Identificação
              </h2>
              <div class="spacer" />
              <h3 class="is-size-2">
                Nome do participante
              </h3>                
            </div>
            <hr class="is-hidden-print">
            <div
              class="flex my-column ca vca inverted  half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-2">
                Dados do Evento
              </h2>
              <h3 class="is-size-3">
                {{ certificate.event_name ? certificate.event_name : 'Nome do Evento' }}
              </h3>
              <div class="spacer" />
              <span class="mb-3"><b>Início esperado:</b><Input
                v-model="certificate.start" 
                type="text"
                maxlength="128"
                class="is-small display-edit"
                placeholder="Início"
                style="text-align: center;"
                @input="changes++"
              /></span>
              <span class="mb-4"><b>Fim esperado:</b><Input
                v-model="certificate.end" 
                type="text"
                maxlength="128"
                class="is-small display-edit"
                placeholder="Fim"
                style="text-align: center;"
                @input="changes++"
              /></span>
              <span><b>Localização:</b> <Input
                v-model="certificate.location" 
                type="text"
                maxlength="128"
                class="is-small display-edit"
                placeholder="Localização"
                style="text-align: center;"
                required
                @input="changes++"
              /></span>
              <br>
              <br>
              <span><b>Acesso Wi-Fi</b></span>
              <br>
              <span><b>SSID: </b> ualg-conferencias</span>
              <span><b>Password: </b> Não sei :(</span>
              <br><br>
              <span><b>Contactos de emergência</b></span>
              <br>
              <span class="mb-2"><Input
                v-model="certificate.emergencyContact1"
                class="display-edit is-small"
                type="text"
                maxlength="64"
                placeholder="Contacto de Emergência"
                style="text-align: center;"
                @input="changes++"
              /></span>
              <span><Input
                v-model="certificate.emergencyContact2"
                class="display-edit is-small"
                type="text"
                maxlength="64"
                placeholder="Contacto de Emergência"
                style="text-align: center;"
                @input="changes++"
              /></span>
            </div>
            <hr class="is-hidden-print">
          </div>
        </div>
      </div>
      <Submit
        id="submit"
        style="width: fit-content;"
        :hide="changes == 0"
        float-bottom="true"
        :error="response && response.id === undefined"
        error-message="Houve um erro na submissão do bilhete."
        success-duration="3000"
        :success-message="success"
        :loading="loading"
        submit-text="Guardar"
        red-green="true"
        cancel-confirmation="Pretende eliminar as suas alterações?"
        submit-confirmation="Pretende guardar as suas alterações?"
        cancel-conf-button="Eliminar"
        submit-conf-button="Guardar"
        @leave="cancel()"
        @dismissed="this.response = undefined; this.success = undefined"
      />
    </form>
  </div>
</template>

<script>
import Input from "@/components/Input";
import Submit from "@/components/Submit";
import { getCookie } from "@/common";
import { API } from "@/http-common";
import VueQrcode from '@chenfengyuan/vue-qrcode';

export default {
  name: "CreateTicket",
  components: {VueQrcode, Input, Submit},
  data() {
    return {
      certificate : {
        event : this.$route.params.id
      },
      response : undefined,
      success : undefined,
      changes : 0,
      loading : undefined,
      invalid: false,
      validity_valid : true,
      loading_publish : false
    }
  },
  created() {
    document.title = "Criar Bilhete de Acesso";
    API.get("ticket/?event=".concat(this.$route.params.id))
      .then((response) => {
          if (response.data[0]) {
            this.certificate = response.data[0]
             document.title = "Editar Bilhete de Acesso";
          }
      }
    ).catch();
  },
  methods : {
    cancel() {
      this.loading = true;
      if (this.$route.params.id) {
        API.get("ticket/?event=".concat(this.$route.params.id))
          .then((response) => {
              this.certificate = response.data[0]
              this.changes = 0;
              this.loading = undefined;
          }
        );
      }
      else
        this.$router.back();
    },
    submit() {
      if (this.$refs.form.checkValidity()) {
        this.loading = true;
        setTimeout(this.postSubmission.bind(this), 0);
      }
    },
    activate() {
      this.loading_publish = true;
      var method;
      var url;
      var successMessage;
      method = "patch";
      url = "/ticket/".concat(this.certificate.id).concat("/");
      successMessage = "O bilhete foi " + (this.certificate.available ? 'desativado' : 'emitido') + " com sucesso. Pode continuar a editar.";
      var options = {
        method : method,
        url : url,
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {"available" : !this.certificate.available},
        transformResponse: [
          async (data) => {
            this.loading = undefined;
            try {
              this.response = JSON.parse(data);
              this.certificate = this.response;
              this.success = successMessage;
              this.loading_publish = false;
            } catch (e) {this.response = data;}
            return this.response;
          },
        ],
      };
      API(options);
    },
    postSubmission() {
      var method;
      var url;
      var successMessage;
      if (this.certificate.id === undefined)  {
          method = "post";
          url = "/ticket/";
          successMessage = "O bilhete foi criado com sucesso. Pode continuar a editar.";
      }
      else {
        method = "put";
        url = "/ticket/".concat(this.certificate.id).concat("/");
        successMessage = "O bilhete foi editado com sucesso. Pode continuar a editar.";
      }
      var options = {
        method : method,
        url : url,
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: this.certificate,
        transformResponse: [
          async (data) => {
            this.loading = undefined;
            try {
              this.response = JSON.parse(data);
              this.certificate = this.response;
              this.success = successMessage;
              this.changes = 0;
            } catch (e) {this.response = data;}
            return this.response;
          },
        ],
      };
      API(options);
    },
    valid() {
      return !this.invalid || this.$refs['input'].validity.valid;
    },
    updateValidity() {
      this.validity_valid = this.$refs['input'].validity.valid;
    }
  }
};
</script>

<style lang="scss">
  @import url(//db.onlinewebfonts.com/c/3b0ae6af94805b08b11cdf1d97c47dce?family=PreloSlabW01-Medium);

  #printarea {
    display: none;
  }

  #printarea>div {
    padding: 1cm;
  }
  #header-area {
    padding-left: 2mm;
    padding-right: 2mm;
  }

  #printarea .row, #editarea .row {
    flex-direction: column;
  }

  #submit .row {
    flex-direction: row;
  }


  @media print {

    #printarea {
      display: block;
    }

    .is-hidden-mobile, .is-hidden {
      display: flex !important;
    }

    .inverted {
        transform: rotate(180deg);
    }

    .is-hidden-print {
      display: none;
    }

    #printarea .row {
      flex-direction: row;
    }

    .half-width {
      width: 50%;
    }

    .height-half {
      height: 50vh;
    }

    .sb {
      justify-content: space-between !important;
    }

    #page-content {
      margin: 0;
    }
    #page-content * {
      visibility: hidden;
    }
    #printarea, #printarea * {
      visibility: visible;
    }

    #printarea {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    #printarea>div {
      height: 100vh;
      overflow: hidden;
      margin: 0;
      padding: 0;
    }
    
    #msg, #main-navbar, #top-hero, footer {
      display: none;
    }

    .date {
      margin-top: 0;
    }
  }

  #printarea {
    background-color: white;
    white-space: break-spaces;
    word-break: break-word;
  }
  h1, h2, h3, span {
    max-width: 20cm;
    text-align: center;
  }
  @page {
    size: A4;
  }

  .date {
    margin-top: 1cm;
  }
  
  h1, h2, h3 {
    margin: 3mm;
  }

  .half-spacer{
    height: 5mm;
  }

  .spacer{
    height: 1cm;
  }

  #printarea * {
    box-sizing: border-box;
    font-family: "PreloSlabW01-Medium";
    page-break-after: avoid;
  }

  .flex {
    display: flex;
  }

  .row {
    flex-direction: row;
  }

  .my-column {
    flex-direction: column;
  }

  .ca {
    justify-content: center;
  }

  .sb {
    justify-content: center;
  }

  .sa {
    justify-content: space-around;
  }

  .vca {
    align-items: center;
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>
